import React, { Component } from 'react';
import { Link, Route, Switch } from 'react-router-dom';
import { withRouter } from 'react-router';
import Alcohol from './Alcohol';


class Acne extends Component {

  constructor(props) {
    super(props);
    this.state = {
      tags: { alcohol: "Alcohol", azelaicacid: "Azelaic Acid", benzoylperoxide: "Benzoyl Peroxide", bha: "BHA", retinol: "Retinol", salicylicacid: "Salicylic Acid" }
    };
  }

  render() {
    return (
      <div>

        <p className="sub-title">Acne</p>

        <div className="tags">
          {Object.keys(this.state.tags).map(key => (
            <div className="singletag" key={key}>
              <Link to={`/acne/${key}`} className="homepage-link">{this.state.tags[key]}</Link>
            </div>
          ))}
        </div>

        <Switch>
          <Route exact path="/" component={Acne} />
          <Route exact path="/alcohol" component={Alcohol} />
        </Switch>

      </div>
    )
  }

}

export default withRouter(Acne);


